body {
    font-family: Noto Sans, ui-sans-serif, system-ui, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Helvetica Neue, Arial, Noto Sans, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", Segoe UI Symbol, "Noto Color Emoji";
}

p {
    font-size: 14px;
    font-weight: 300;
    letter-spacing: .01rem;
}

h5 {
    font-size: 12px;
    font-weight: 400;
    margin-top: 1px;
    margin-bottom: 1px;
}

a {
    color: inherit;
}

h1 {
    font-weight: 500;
}


ul {
    list-style-type: none;
    width: 15%;
    margin: auto;
}

b {
    font-weight: 500;
}

.nameBoxDiv {
    background-image: url("res/nametag_bw.png");
    filter: "sepia";
    background-size: 100% 100%;
    width: 200px;
    height: 40px;
    margin-bottom: 15px;
}

.nameBoxDivSmall {
    background-image: url("res/nametag_bw.png");
    filter: "sepia";
    background-size: 100% 100%;
    width: 80px;
    height: 16px;
    margin: 0 auto;
}

.nameBoxDivChrome {
    background: black;
    color: white;
    font-family: "Futura", "Nunito Sans";
    font-weight: bold;
    font-size: 1.6rem;
    font-style: italic;
    text-align: center;
    width: 200px;
    height: 37px;
    margin-bottom: 15px;
}

.nameBoxDivChromeSmall {
    background: black;
    color: white;
    font-family: "Futura", "Nunito Sans";
    font-weight: bold;
    font-size: 1.6rem;
    font-style: italic;
    text-align: center;
    width: 150px;
    height: 32px;
    margin-bottom: 15px;
}


@keyframes gradient {
    0% {
        background-position: 0% 50%;
    }

    50% {
        background-position: 100% 50%;
    }

    100% {
        background-position: 0% 50%;
    }
}

@keyframes nameBoxDiv {
    0% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}

.footerLink :link {
    font-size: smaller;
    text-decoration: none;
    color: inherit;
}

.footerLink :visited {
    text-decoration: none;
}

.footerLink :hover {
    text-decoration: none;
    background-color: #d6d6d6;
    color: inherit;
}

.hoverLink2 :link {
    text-decoration: none;
    color: inherit;
}

.hoverLink2 :hover {
    text-decoration: none;
    background-color: #4e4e4e;
    color: inherit;
}

.hoverLink :link {
    text-decoration: none;
    color: inherit;
}

.hoverLink :hover {
    text-decoration: none;
    background-color: #d7d7d7;
    color: inherit;
}

.routerDiv {
    text-align: left;
}

.routerLink :link {
    text-decoration: none;
    color: inherit;
    margin-bottom: 31px;
}

.routerLink :hover {
    text-decoration: none;
    background-color: grey;
    color: white;
    margin-bottom: 31px;
}


.selectorBox {
    display: flex
}

.selector {

    flex: 1;
    text-align: center;
}

input[type="search"] {
    white-space: pre-wrap;
    word-wrap: break-word
}

#class-chart {
    height: 70px;
    position: relative;
    z-index: 1;

}

#first {
    position: absolute;
    z-index: 2;
}